*{font-family:"Be Vietnam Pro",sans-serif}h1{color:#ff0000}a{color:unset;text-decoration:none}.section__padding{padding:0 10rem}@media (max-width: 992px){.section__padding{padding:0 8rem}}@media (max-width: 768px){.section__padding{padding:0 4rem}}@media (max-width: 576px){.section__padding{padding:0 2rem}}.section__margin{margin:0 10rem}@media (max-width: 992px){.section__margin{margin:0 8rem}}@media (max-width: 768px){.section__margin{margin:0 4rem}}@media (max-width: 576px){.section__margin{margin:0 2rem}}.fade-in{-webkit-animation:fade-in 300ms ease-in-out;animation:fade-in 300ms ease-in-out}@-webkit-keyframes fade-in{from{opacity:0}to{opacity:1}}@keyframes fade-in{from{opacity:0}to{opacity:1}}

.navbar{display:flex;justify-content:space-between;align-items:center;padding-top:4rem;padding-bottom:4rem}@media (max-width: 768px){.navbar{padding-top:2.5rem;padding-bottom:2.5rem}}.navbar__links{display:flex;list-style:none}.navbar__links a{color:#242d52;font-size:13px;font-weight:500;transition:color 200ms ease-in}.navbar__links a:hover{color:#9095a7}.navbar__links a:not(:last-child){margin-right:2rem}@media (max-width: 992px){.navbar__links{display:none}}@media (max-width: 992px){.navbar__btn{display:none}}.navbar__menu-container{position:absolute;top:7rem;left:2rem;right:2rem;text-align:center;background:#fafafa;padding:2rem 3rem;z-index:2}.navbar__menu-links{font-size:18px;font-weight:500}@media (min-width: 993px){.navbar__menu{display:none}}

.btn{background:#f25f3a;color:#ffefeb;padding:1rem 2rem;font-size:14px;font-weight:500;border:none;border-radius:10rem;box-shadow:0px 4px 10px 2px rgba(243,97,60,0.3);cursor:pointer;transition:opacity 200ms ease-in}.btn:hover{opacity:0.7}

